// api/active-user.js

import axios from 'axios'

const API_URL = 'http://jasdig.com:5154/apron/api'
// const API_URL = 'http://192.168.88.45:5154/apron/api'
const API_URL_DEMO = 'http://jasdig.com:5153/apron/api'

export function url_login(userData) {
  return axios.post(`${API_URL}/login`, userData)
}
export function url_logout(userData) {
  return axios.post(`${API_URL}/logout`, userData)
}

//-----------------------------------------------------
export function url_find_info_jukir_by_range() {
  return axios.post(`${API_URL_DEMO}/find_info_jukir_by_range?stdate=now&endate=""`)
}

export function url_find_info_jukir_by_mode(userData) {
  return axios.post(`${API_URL_DEMO}/find_info_jukir_by_range`,null, {params:userData})
}

export function url_find_info_jukir(userData) {
  return axios.post(`${API_URL_DEMO}/find_info_jukir`,null, {params:userData})
}

export function url_find_info_jukir_by_id(userData) {
  return axios.post(`${API_URL_DEMO}/find_info_jukir_by_id`,null, {params:userData})
}

export function url_find_info_jukir_by_full_id(userData) {
  return axios.post(`${API_URL_DEMO}/find_info_jukir_full_id`, userData)
}
